import React, { memo, useCallback, useRef, useState } from "react";
import {
  AnimatedCollapseMethod,
  AnimatedCollapseWrapper,
  CustomSwitch,
} from "@/components";
import Animated, {
  interpolateColor,
  runOnUI,
  SharedValue,
  useAnimatedStyle,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from "react-native-reanimated";
import { Colors, styled, translate, useTheme } from "@true/core";
import { UIInput, UIText } from "@true/ui";
import { Icons } from "@true/asset";
import { FlatList, TouchableOpacity } from "react-native";

interface Props {
  params: SharedValue<{ [p: string]: any }>;
  setSelectedItems: Function;
  setTypeRepeated: Function;
  selectedItems: number[];
}

export interface ICheckboxParams {
  has_weekly: number;
  has_monthly: number;
}

export const RepeatedBooking = memo(
  ({ params, selectedItems, setSelectedItems, setTypeRepeated }: Props) => {
    const animatedSwitch = useSharedValue(false);
    const isChangeText = useSharedValue(false);
    const days = [
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday",
      "Sunday",
      "",
    ];

    const valueCheckbox = useSharedValue<ICheckboxParams>({
      has_weekly: 0,
      has_monthly: 0,
    });

    const collapseRefWeekly = useRef<AnimatedCollapseMethod | null>(null);
    const collapseRefMonthly = useRef<AnimatedCollapseMethod | null>(null);

    const currentValueWeekly = valueCheckbox
      ? useDerivedValue(
          () => valueCheckbox.value["has_weekly"] || 0,
          ["has_weekly", valueCheckbox]
        )
      : useSharedValue(0);

    const currentValueMonthly = valueCheckbox
      ? useDerivedValue(
          () => valueCheckbox.value["has_monthly"] || 0,
          ["has_monthly", valueCheckbox]
        )
      : useSharedValue(0);

    const onSwitch = useWorkletCallback(
      (keyName: string, value: boolean) => {
        animatedSwitch.value = value;
        if (params) {
          params.value = {
            ...params.value,
            [keyName]: value ? 1 : 0,
            ["resource_pick_booking_type"]: value ? "repeated" : "single",
          };
        }
      },
      [animatedSwitch, params]
    );

    const onChangeValue = useCallback(
      (key: string, value: any) => {
        runOnUI((key: string, value: any) => {
          "worklet";

          if (key === "monthly_dates") {
            isChangeText.value = true;
          }

          if (params) {
            params.value = {
              ...params.value,
              [key]: value,
            };
          }
        })(key, value);
      },
      [isChangeText, params]
    );

    const animatedStyle = useAnimatedStyle(() => {
      return {
        maxHeight: withTiming(animatedSwitch.value ? 500 : 0, {
          duration: 500,
        }),
        overflow: "scroll",
      };
    }, [animatedSwitch]);

    const onPressCheckBoxWeekly = useCallback(
      (value: number) => {
        collapseRefWeekly?.current?.press?.(value);
        collapseRefMonthly?.current?.press?.(0);
        setTypeRepeated("weekly");
        valueCheckbox.value = {
          ...valueCheckbox.value,
          ["has_weekly"]: value,
          ["has_monthly"]: 0,
        };
      },
      [collapseRefWeekly, valueCheckbox, setTypeRepeated]
    );

    const onPressCheckBoxMonthly = useCallback(
      (value: number) => {
        collapseRefMonthly?.current?.press?.(value);
        collapseRefWeekly?.current?.press?.(0);
        setTypeRepeated("monthly");
        valueCheckbox.value = {
          ...valueCheckbox.value,
          ["has_monthly"]: value,
          ["has_weekly"]: 0,
        };
      },
      [collapseRefMonthly, valueCheckbox, setTypeRepeated]
    );

    // const [selectedItems1, setSelectedItems1] = useState<number[]>([]);

    const toggleItem = (item: string, index: number) => {
      if (selectedItems.includes(index)) {
        // Nu index  c chn, loi b n khi mng
        // setSelectedItems1(selectedItems.filter((i) => i !== index));
        setSelectedItems(selectedItems.filter((i) => i !== index));
        params.value["weekly_dates"] = selectedItems.filter((i) => i !== index);
      } else {
        // Nu index cha c chn, thm n vo mng
        // setSelectedItems1([...selectedItems, index]);
        setSelectedItems([...selectedItems, index]);
        params.value["weekly_dates"] = [...selectedItems, index];
      }
    };

    return (
      <SWrapper>
        <FlexBox>
          <SText>{translate("booking.repeated_booking")}</SText>
          <CustomSwitch
            keyName={"resource_pick_booking_type"}
            onPress={onSwitch}
            animatedSwitch={animatedSwitch}
            activeTrackColor={Colors.blue1}
            scale={22}
          />
        </FlexBox>
        <Animated.View style={animatedStyle}>
          <SContainer>
            <SRow>
              <Checkbox
                currentValue={currentValueWeekly}
                title={translate("booking.weekly")}
                onPressCheckBox={onPressCheckBoxWeekly}
              />
              <Checkbox
                currentValue={currentValueMonthly}
                title={translate("booking.monthly")}
                onPressCheckBox={onPressCheckBoxMonthly}
              />
            </SRow>
          </SContainer>
          <SContainer>
            <AnimatedCollapseWrapper ref={collapseRefWeekly}>
              <SCollapseWrapper>
                <STextRepeated>
                  {translate("booking.repeated_days")} *
                </STextRepeated>
                <DaysContainer>
                  <FlatList
                    data={days}
                    numColumns={2}
                    keyExtractor={(item, index) => item + index}
                    renderItem={({ item, index }) => (
                      <SViewDay
                        disabled={index === 7}
                        index={index}
                        selected={selectedItems.includes(index)}
                        onPress={() => toggleItem(item, index)}
                      >
                        {selectedItems.includes(index) && index !== 7 ? (
                          <>
                            <TriangleView />
                            <SIcon source={Icons.Filled.CHECK} />
                          </>
                        ) : null}
                        <STextDay selected={selectedItems.includes(index)}>
                          {item}
                        </STextDay>
                      </SViewDay>
                    )}
                  />
                </DaysContainer>
              </SCollapseWrapper>
            </AnimatedCollapseWrapper>

            <AnimatedCollapseWrapper ref={collapseRefMonthly}>
              <SCollapseWrapper>
                <UIInput.Text
                  keyName={"monthly_dates"}
                  params={params}
                  title={translate("booking.repeated_days")}
                  onFocus={() => (isChangeText.value = true)}
                  onEndEditing={() => (isChangeText.value = false)}
                  onChangeValue={onChangeValue}
                  required
                  style={{ marginHorizontal: 12 }}
                />
                <SNotes>{translate("booking.note_repeated_monthly")}</SNotes>
              </SCollapseWrapper>
            </AnimatedCollapseWrapper>
          </SContainer>
        </Animated.View>
      </SWrapper>
    );
  }
);

const AnimatedTouchableOpacity =
  Animated.createAnimatedComponent(TouchableOpacity);

interface ICheckbox {
  title: string;
  currentValue: SharedValue<number>;
  onPressCheckBox: (value: number) => void;
}

const Checkbox = memo(({ title, currentValue, onPressCheckBox }: ICheckbox) => {
  const theme = useTheme();

  const onPress = useCallback(() => {
    onPressCheckBox(currentValue.value ? 0 : 1);
  }, [currentValue, onPressCheckBox]);

  const animatedTickStyle = useAnimatedStyle(() => {
    const active = currentValue.value;
    return {
      borderColor: interpolateColor(
        active,
        [0, 1],
        [theme.neutral2, theme.blue1]
      ),
    };
  }, [theme]);

  const animatedTickedStyle = useAnimatedStyle(() => {
    const active = currentValue.value;
    return {
      backgroundColor: interpolateColor(
        active,
        [0, 1],
        [theme.white, theme.blue1]
      ),
    };
  }, [theme]);

  return (
    <SCheckbox onPress={onPress}>
      <STickView style={animatedTickStyle}>
        <STicked style={animatedTickedStyle} />
      </STickView>
      <STextView>
        <STitle>{title}</STitle>
      </STextView>
    </SCheckbox>
  );
});

const SWrapper = styled.View`
  background-color: ${(p) => p.theme.white};
  border-radius: 8px;
  margin-bottom: 16px;
  justify-content: center;
  align-content: center;
  border-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

