import constate from "constate";
import { useCallback, useMemo, useRef } from "react";
import Animated, {
  useAnimatedRef,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import { LayoutChangeEvent } from "react-native";
import moment from "moment";
import { EFilterType } from "@/screens/CalendarScreen/type";

const _provider = () => {
  const heightScrollableTab = useSharedValue(0);
  const showCalendar = useSharedValue(0);
  const selectedDateString = useSharedValue(moment().format("YYYY-MM-DD"));
  const currentTabIndex = useSharedValue(0);

  const isTouchActiveNative = useSharedValue(false);

  const containerSize = useSharedValue(0);
  const scrollViewSize = useSharedValue(0);
  const scrollOffsetY = useSharedValue(0);
  const scrollInit = useSharedValue(0);
  const touchTranslate = useSharedValue(0);

  const aViewRef = useAnimatedRef<Animated.View>();

  const scrollViewRef = useRef<Animated.ScrollView>(null);

  const panTopDotRef = useRef();
  const panBottomDotRef = useRef();
  const panBlockRef = useRef();

  const posSelectTime = {
    top: useSharedValue(0),
    showDateTop: useSharedValue(false),
    showDateBottom: useSharedValue(false),
    show: useSharedValue(false),
    date: useSharedValue(""),
    height: useSharedValue(0),
  };

  // useAnimatedReaction(
  //   () => ({
  //     _isTouchActiveNative: isTouchActiveNative.value,
  //     _show: posSelectTime.show.value,
  //   }),
  //   (cur, prev) => {
  //     if (
  //       cur._isTouchActiveNative &&
  //       cur._isTouchActiveNative !== prev?._isTouchActiveNative
  //     ) {
  //       console.log("save");
  //       scrollInit.value = scrollOffsetY.value;
  //     }
  //   }
  // );

  // dropdown to display select booking type
  const selectedType = useSharedValue("schedule");
  const showSelectType = useSharedValue(0);
  const positionSelectType = {
    top: useSharedValue(0),
    left: useSharedValue(0),
  };

  const onResetPosSelectTime = useCallback(() => {
    posSelectTime.top.value = 0;
    posSelectTime.show.value = false;
    posSelectTime.showDateTop.value = false;
    posSelectTime.showDateBottom.value = false;
    posSelectTime.date.value = "";
    posSelectTime.height.value = 0;
  }, []);

  const onShowFilterType = useWorkletCallback((type: EFilterType) => {
    switch (type) {
      case EFilterType.CALENDAR:
        showCalendar.value = showCalendar.value ? 0 : 1;
        showSelectType.value = 0;
        break;
      case EFilterType.BOOKING_TYPE:
        showCalendar.value = 0;
        showSelectType.value = showSelectType.value ? 0 : 1;
        break;
    }
  });

  const onLayoutScrollableTab = useWorkletCallback(
    ({
      nativeEvent: {
        layout: { height },
      },
    }: LayoutChangeEvent) => {
      heightScrollableTab.value = height;
    },
    []
  );
  return useMemo(
    () => ({
      onLayoutScrollableTab,
      heightScrollableTab,
      showCalendar,
      selectedDateString,
      currentTabIndex,
      selectedType,
      showSelectType,
      positionSelectType,
      onShowFilterType,
      posSelectTime,
      panBlockRef,
      panTopDotRef,
      panBottomDotRef,
      scrollViewRef,
      onResetPosSelectTime,
      scrollViewSize,
      containerSize,
      scrollOffsetY,
      scrollInit,
      isTouchActiveNative,
      touchTranslate,
      aViewRef,
    }),
    [
      onLayoutScrollableTab,
      heightScrollableTab,
      showCalendar,
      selectedDateString,
      currentTabIndex,
      selectedType,
      showSelectType,
      positionSelectType,
      onShowFilterType,
      posSelectTime,
      panBlockRef,
      panTopDotRef,
      panBottomDotRef,
      scrollViewRef,
      onResetPosSelectTime,
      scrollViewSize,
      containerSize,
      scrollOffsetY,
      scrollInit,
      isTouchActiveNative,
      touchTranslate,
      aViewRef,
    ]
  );
};

export const [ResourceBookingProvider, useResourceBookingProvider] = constate(
  _provider,
  (val) => val
);
