import range from "lodash/range";
import times from "lodash/times";
import React, { memo, useCallback, useMemo, useRef } from "react";
import { TouchableWithoutFeedback, View } from "react-native";
import {
  buildHourAndMinuteString,
  calcTimeByPosition,
  constants,
} from "../../../utils";
import {
  buildUnavailableHoursBlocks,
  HOUR_BLOCK_HEIGHT,
} from "react-native-calendars/src/timeline/Packer";
import {
  buildTimeString,
  calcDateByPosition,
} from "react-native-calendars/src/timeline/helpers/presenter";
import { TimelineHoursProps } from "@/screens/CalendarScreen/type";
import { GestureResponderEvent } from "react-native/Libraries/Types/CoreEventTypes";
import { memoForwardRef, styled } from "@true/core";
import { UIText } from "@true/ui";
import Animated from "react-native-reanimated";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";
import { PanGestureSelectTime } from "@/screens/CalendarScreen/components/calendar/MyTimeLine/PanGestureSelectTime";

const dimensionWidth = constants.screenWidth;
export const HOURS_TEXT_WIDTH = 34;
const EVENT_DIFF = 16 + 16 + 8 + HOURS_TEXT_WIDTH;

export const TimelineHours = memoForwardRef(
  (props: TimelineHoursProps, ref: React.Ref<any>) => {
    const {
      format24h,
      start = 0,
      end = 24,
      date,
      unavailableHours,
      unavailableHoursColor,
      styles,
      onBackgroundLongPress,
      onBackgroundLongPressOut,
      width,
      numberOfDays = 1,
      timelineLeftInset = 0,
    } = props;
    const lastLongPressEventTime = useRef<any>();
    const { posSelectTime } = useBookingProvider();
    // const offset = this.calendarHeight / (end - start);
    const offset = HOUR_BLOCK_HEIGHT;
    const unavailableHoursBlocks = buildUnavailableHoursBlocks(
      unavailableHours,
      {
        dayStart: start,
        dayEnd: end,
      }
    );
    const hours = useMemo(() => {
      return range(start, end + 1).map((i) => {
        let timeText;
        if (i === start) {
          timeText = "";
        } else if (i < 12) {
          timeText = !format24h ? `${i} AM` : `${i}:00`;
          if (format24h && i < 10) {
            timeText = `0${i}:00`;
          }
        } else if (i === 12) {
          timeText = !format24h ? `${i} PM` : `${i}:00`;
        } else if (i === 24) {
          timeText = !format24h ? "12 AM" : "24:00";
        } else {
          timeText = !format24h ? `${i - 12} PM` : `${i}:00`;
        }
        return { timeText, time: i };
      });
    }, [start, end, format24h]);
    const handleBackgroundPress = useCallback(
      (event: GestureResponderEvent) => {
        const yPosition = event.nativeEvent.locationY;
        const xPosition = event.nativeEvent.locationX;
        const { hour, minutes, time } = calcTimeByPosition(
          yPosition,
          HOUR_BLOCK_HEIGHT,
          true
        );
        const dateByPosition = calcDateByPosition(
          xPosition,
          timelineLeftInset,
          numberOfDays,
          date
        );
        lastLongPressEventTime.current = {
          hour,
          minutes,
          date: dateByPosition,
        };
        const timeString = buildTimeString(hour, minutes, dateByPosition);
        const _height = posSelectTime.height.value || HOUR_BLOCK_HEIGHT;
        posSelectTime.height.value = _height;
        posSelectTime.top.value = time * HOUR_BLOCK_HEIGHT;
        posSelectTime.show.value = true;
        posSelectTime.date.value = buildHourAndMinuteString(hour, minutes);
        onBackgroundLongPress?.(timeString, lastLongPressEventTime.current);
      },
      [onBackgroundLongPress, date, hours]
    );
    const handlePressOut = useCallback(() => {
      if (lastLongPressEventTime.current) {
        const { hour, minutes, date } = lastLongPressEventTime.current;
        const timeString = buildTimeString(hour, minutes, date);
        onBackgroundLongPressOut?.(timeString, lastLongPressEventTime.current);
        lastLongPressEventTime.current = undefined;
      }
    }, [onBackgroundLongPressOut, date]);

    return (
      <>
        <TouchableWithoutFeedback
          onLongPress={handleBackgroundPress}
          onPressOut={handlePressOut}
        >
          <SAbsView
            style={{
              left: timelineLeftInset
                ? timelineLeftInset - 16
                : HOURS_TEXT_WIDTH + 8,
            }}
          />
        </TouchableWithoutFeedback>
        {unavailableHoursBlocks.map((block, index) => (
          <View
            key={index}
            // @ts-ignore
            style={[
              styles.unavailableHoursBlock,
              block,
              unavailableHoursColor
                ? { backgroundColor: unavailableHoursColor }
                : undefined,
              { left: timelineLeftInset },
            ]}
          ></View>
        ))}

        {hours.map(({ timeText, time }, index) => {
          return (
            <React.Fragment key={time}>
              <SCaption
                key={`timeLabel${time}`}
                style={
                  // styles.timeLabel,
                  {
                    top: offset * index - 8,
                    width: timelineLeftInset - 16,
                  }
                }
              >
                {timeText}
              </SCaption>
              <SLine
                key={`line${time}`}
                // @ts-ignore
                style={[
                  styles.line,
                  {
                    top: offset * index,
                    width: dimensionWidth - EVENT_DIFF,
                    left: timelineLeftInset
                      ? timelineLeftInset - 16
                      : HOURS_TEXT_WIDTH + 8,
                  },
                ]}
              />
              {/*{<View key={`lineHalf${time}`} style={[styles.line, { top: offset * (index + 0.5), width: dimensionWidth - EVENT_DIFF, left: timelineLeftInset - 16 }]}/>}*/}
            </React.Fragment>
          );
        })}
        {times(numberOfDays, (index) => (
          <View
            key={index}
            // @ts-ignore
            style={[
              styles.verticalLine,
              { right: ((index + 1) * width) / numberOfDays },
            ]}
          />
        ))}
        <PanGestureSelectTime
          numberHours={hours.length - 1}
          timelineLeftInset={timelineLeftInset}
          ref={ref}
        />
      </>
    );
  }
);

const SCaption = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
  position: absolute;
  text-align: center;
`;

const SLine = styled.View`
  height: 1px;
  position: absolute;
  background-color: ${(p) => p.theme.neutral5};
`;
const SAbsView = styled(Animated.View)`
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
`;
