import { memo, useCallback } from "react";
import { interactManager, styled } from "@true/core";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";
import { DynamicHeader, UIButton, UIText, UITextStyle } from "@true/ui";
import { Icons } from "@true/asset";
import { useSelectedItems } from "@/screens/CalendarScreen/hooks/useSelectedItems";
import moment from "moment";
import { LayoutChangeEvent } from "react-native";
import { navigateToCreateBooking } from "@/utils/navigation";
import {
  buildHourAndMinuteString,
  calcTimeByPosition,
} from "@/screens/CalendarScreen/utils";
import { HOUR_BLOCK_HEIGHT } from "@/screens/CalendarScreen/packer";

export const SelectTimeHeader = memo(() => {
  const { posSelectTime, onResetPosSelectTime, selectedDateString } =
    useBookingProvider();
  const fixHeight = useSharedValue(0);

  const onLayout = useCallback((e: LayoutChangeEvent) => {
    fixHeight.value = e.nativeEvent.layout.height;
  }, []);

  const animatedStyle = useAnimatedStyle(() => ({
    // zIndex: posSelectTime.show.value ? 1 : -1,
    height: withTiming(posSelectTime.show.value ? fixHeight.value : 0),
  }));

  const onSave = useCallback(() => {
    const { hour: sHour, minutes: sMinutes } = calcTimeByPosition(
      posSelectTime.top.value,
      HOUR_BLOCK_HEIGHT,
      false
    );
    const { hour: eHour, minutes: eMinutes } = calcTimeByPosition(
      posSelectTime.top.value + posSelectTime.height.value,
      HOUR_BLOCK_HEIGHT,
      false
    );

    navigateToCreateBooking({
      date: moment(selectedDateString.value).unix(),
      start_time:
        moment(
          `${selectedDateString.value} ${buildHourAndMinuteString(
            sHour,
            sMinutes
          )}`,
          "YYYY-MM-DD HH:mm"
        ).valueOf() / 1000,
      end_time:
        moment(
          `${selectedDateString.value} ${buildHourAndMinuteString(
            eHour,
            eMinutes
          )}`,
          "YYYY-MM-DD HH:mm"
        ).valueOf() / 1000,
    });
    interactManager(onResetPosSelectTime, 100);
  }, []);

  return (
    <SWrapper style={animatedStyle}>
      <Animated.View onLayout={onLayout}>
        <DynamicHeader
          leftComponent={<SCloseBtn onPress={onResetPosSelectTime} />}
          title={"Selecting time"}
          rightComponent={<SSaveBtn onPress={onSave} />}
          hideSeparator={true}
        />
      </Animated.View>
    </SWrapper>
  );
});

const SWrapper = styled(Animated.View)`
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  background-color: ${(p) => p.theme.white};
  overflow: scroll;
`;

const SCloseBtn = styled(UIButton).attrs((p) => ({
  source: Icons.Outlined.X_MARK,
  imageSize: 24,
}))`
  padding-left: 16px;
`;

const SSaveBtn = styled(UIButton).attrs((p) => ({
  title: "Save",
  textStyle: {
    ...UITextStyle.button_medium,
    color: p.theme.white,
  },
}))`
  width: 90px;
  height: 32px;
  padding: 4px 16px;
  justify-content: center;
  align-items: center;
  background-color: ${(p) => p.theme.primaryColor};
  border-radius: 8px;
  margin-right: 16px;
`;
