import React, { memo, useCallback } from "react";
import {
  Fonts,
  goBack,
  styled,
  translate,
  useAsyncFn,
  useNavigationParams,
  useTheme,
} from "@true/core";
import {
  LeftModalHeader,
  ScreenWrapper,
  UIBottomButton,
  UIButton,
  UIInput,
} from "@true/ui";
import { StyleSheet } from "react-native";
import { Icons } from "@true/asset";
import { runOnUI, useSharedValue } from "react-native-reanimated";
import moment from "moment";
import { requestAvailableResoures } from "@/store/resources/function";

export interface ISortByTimeParams {
  "start-date": number;
  "start-time": number;
  "end-date": number;
  "end-time": number;
}

export interface SelectTimeScreenProps {
  start: number;
  end: number;
  setStart: any;
  setEnd: any;
}

export const SelectTimeScreen = memo(() => {
  const { start, end, setStart, setEnd } = useNavigationParams<SelectTimeScreenProps>();
  const theme = useTheme();
  const params = useSharedValue<ISortByTimeParams>({
    "start-date": start ? start : moment().unix(),
    "start-time": start ? start : 0,
    "end-date": end ? end : 0,
    "end-time": end ? end : 0,
  });

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value: any) => {
        "worklet";

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params]
  );

  const onReset = useCallback(async () => {
    setStart(0);
    setEnd(0);
    goBack();
  }, []);

  const [{ loading, error: errorCreate }, onApply] = useAsyncFn(async () => {
    const startTime = moment.unix(params?.value["start-time"]).format("HH:mm");
    const endDate = moment.unix(params?.value["end-date"]).format("DD/MM/YYYY");
    const endTime = moment.unix(params?.value["end-time"]).format("HH:mm");
    const startDate = moment
      .unix(params?.value["start-date"])
      .format("DD/MM/YYYY");

    const startDateTime = moment(
      `${startDate} ${startTime}`,
      "DD/MM/YYYY HH:mm"
    );
    const endDateTime = moment(`${endDate} ${endTime}`, "DD/MM/YYYY HH:mm");

    const startTimestamp = startDateTime.unix();
    const endTimestamp = endDateTime.unix();
    await requestAvailableResoures({
      start: startTimestamp,
      end: endTimestamp,
    }).then();
    setStart(startTimestamp);
    setEnd(endTimestamp);
    goBack();
  }, [params, setStart]);

  return (
    <SScreenWrapper>
      <LeftModalHeader
        title={translate("booking.sort_by_time")}
        rightComponent={
          <SCloseButton source={Icons.Filled.X_MARK} onPress={goBack} />
        }
      />
      <SContainer>
        <SRow>
          <UIInput.DateTime
            keyName={"start-date"}
            params={params}
            title={translate("booking.start_date")}
            style={{ flex: 1, marginRight: 12 }}
            mode={"date"}
            onChangeValue={onChangeValue}
            required
          />
          <UIInput.DateTime
            keyName={"start-time"}
            params={params}
            title={translate("booking.start_time")}
            style={{ flex: 1 }}
            mode={"time"}
            onChangeValue={onChangeValue}
            required
          />
        </SRow>

        <SRow>
          <UIInput.DateTime
            keyName={"end-date"}
            params={params}
            title={translate("booking.end_date")}
            style={{ flex: 1, marginRight: 12 }}
            mode={"date"}
            onChangeValue={onChangeValue}
            required
          />
          <UIInput.DateTime
            keyName={"end-time"}
            params={params}
            title={translate("booking.end_time")}
            style={{ flex: 1 }}
            mode={"time"}
            onChangeValue={onChangeValue}
            required
          />
        </SRow>
      </SContainer>
      <SBottomButton
        buttons={[
          {
            title: translate("booking.reset"),
            onPress: onReset,
            textColor: theme.primaryColor,
            backgroundColor: theme.white,
            style: {
              borderWidth: 1,
              borderColor: theme.primaryColor,
            },
            imageStyle: {
              tintColor: theme.primaryColor,
            },
            textStyle: styles.textStyle,
          },
          {
            title: translate("booking.apply"),
            onPress: onApply,
            textColor: theme.white,
            backgroundColor: theme.primaryColor,
            textStyle: { ...styles.textStyle, marginRight: 0 },
          },
        ]}
      />
    </SScreenWrapper>
  );
});

const styles = StyleSheet.create({
  textStyle: {
    fontFamily: Fonts.Medium,
    fontSize: 16,
    lineHeight: 24,
    marginRight: 8,
  },
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.white};
`;

const SCloseButton = styled(UIButton)`
  width: 24px;
  height: 24px;
  margin-right: 16px;
`;

const SContainer = styled.View`
  margin-top: 14px;
  flex: 1;
`;

const SRow = styled.View`
  flex-direction: row;
  margin: 6px 16px;
  align-items: center;
`;

const SBottomButton = styled(UIBottomButton)`
  padding: 8px 16px;
  margin: 0;
  border-top-width: 1px;
  border-color: ${(p) => p.theme.neutral6};
  background-color: ${(p) => p.theme.white};
`;
