import React, { memo } from "react";
import FastImage from "react-native-fast-image";
import { Dimensions } from "react-native";
import { styled, translate } from "@true/core";
import { UIRow, UIText } from "@true/ui";

interface Props {
  source: any;
  title: string;
  subTitle: string;
  hasTypingRule?: boolean;
}

export const EmptyViewCenter = memo(
  ({ source, title, subTitle, hasTypingRule }: Props) => {
    return (
      <SView>
        <SImage source={source} resizeMode={"center"} />
        <SBoldTextEmpty>{title}</SBoldTextEmpty>
        <STextEmpty>{subTitle}</STextEmpty>
        {hasTypingRule ? (
          <SViewRule>
            <UIRow
              subtitle={translate("booking.check_spelling")}
              leftComponent={<SViewDot />}
            />
            <UIRow
              subtitle={translate("booking.try_again_other_keywords")}
              leftComponent={<SViewDot />}
            />
            <UIRow
              subtitle={translate("booking.try_again_shorted_keywords")}
              leftComponent={<SViewDot />}
            />
            <UIRow
              subtitle={translate("booking.try_again_general_keywords")}
              leftComponent={<SViewDot />}
            />
          </SViewRule>
        ) : null}
      </SView>
    );
  }
);

const screenHeight = Dimensions.get("screen").height;

const SBoldTextEmpty = styled(UIText.BodyLarge500)`
  color: ${(p) => p.theme.neutral1};
  text-align: center;
`;

const STextEmpty = styled(UIText.BodyMedium400)`
  color: ${(p) => p.theme.neutral2};
  text-align: center;
`;

const SView = styled.View`
  flex: 1;
  align-items: center;
  justify-content: center;
  margin-top: ${screenHeight / 4}px;
`;

const SImage = styled(FastImage)`
  height: 140px;
  width: 140px;
`;

const SViewRule = styled.View`
  flex: 1;
  width: 80%;
  border-radius: 8px;
  border-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
  margin: 8px 16px;
  justify-content: center;
  align-items: center;
  padding: 12px 0;
`;

const SViewDot = styled.View`
  height: 4px;
  width: 4px;
  border-radius: 4px;
  background-color: ${(p) => p.theme.neutral2};
  margin-top: 4px;
  margin-right: 12px;
`;
