import React, { memo, useCallback, useMemo } from "react";
import { styled, translate, useNavigationParams } from "@true/core";
import { DynamicHeader, ScreenWrapper, UIFlashList } from "@true/ui";
import { useResourceGroups } from "@/store/groups";
import { ResourceItem } from "@/screens/ResoucesScreen/components/ResourceItem";
import { getResources, getResourcesByQuery } from "@/store/resources";
import { IMAGE_EMPTY_LIST_RESOURCE } from "@true/asset";
import { EmptyViewCenter } from "@/components/EmptyView/EmptyViewCenter";

export interface ResourceListScreenProps {
  id: number | string;
  start: number;
}

export const ResourceListScreen = memo(() => {
  const { id, start } = useNavigationParams<ResourceListScreenProps>();
  const group = useResourceGroups(id);

  const resourceIds = useMemo(() => {
    return start !== 0
      ? getResourcesByQuery("available")
      : getResourcesByQuery("all");
  }, [start]);

  const resourceInGroup = useMemo(() => {
    let resourceInGroup: string[] = [];
    resourceIds.map((resourceId) => {
      if (getResources(resourceId)?.group_id == id)
        resourceInGroup.push(resourceId);
    });
    return resourceInGroup;
  }, [resourceIds, id]);

  const renderItem = useCallback(
    ({ item, index }: { item: number | string; index?: number }) => (
      <ResourceItem key={index} id={item} index={index} />
    ),
    []
  );

  const renderEmptyView = useCallback(() => {
    return (
      <EmptyViewCenter
        source={IMAGE_EMPTY_LIST_RESOURCE}
        title={translate("booking.empty_resource_group")}
        subTitle={translate("booking.no_resource_in_group")}
      />
    );
  }, []);

  return (
    <SScreenWrapper>
      <DynamicHeader title={group?.name} />
      <UIFlashList
        data={resourceInGroup}
        renderItem={renderItem}
        showsVerticalScrollIndicator={false}
        estimatedItemSize={100}
        // loading={loading}
        // onLoadData={onLoadData}
        // refreshable={true}
        // refreshing={loading && !loadMore}
        // loadMorable={true}
        // loadingMore={loadMore}
        ListEmptyComponent={renderEmptyView}
      />
    </SScreenWrapper>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.white};
  flex: 1;
`;
