import { useCallback, useEffect, useMemo, useState } from "react";
import { Event } from "@/screens/CalendarScreen/type";
import {
  runOnJS,
  useAnimatedReaction,
  useSharedValue,
} from "react-native-reanimated";
import {
  getBookings,
  getBookingsByQuery,
  useBookingsByQuery,
} from "@/store/booking";
import moment from "moment/moment";
import { interactManager, useAsyncFn, useAutoToastError } from "@true/core";
import { requestBookings } from "@/store/booking/function";
import { useResourceBookingProvider } from "@/screens/ResourceDetailScreen/provider";
import { getDSEndOfTFromDS, getDSStartOfTFromDS } from "@/utils/calendar";

export const useRequestResourceBookings = ({
  index,
  query,
  tab,
  params,
  mainKey = "filter",
  resource_id,
}: {
  index: number;
  tab?: string;
  query?: string;
  params?: any;
  mainKey?: string;
  resource_id?: string;
}) => {
  const [events, setEvents] = useState<Event[]>([]);
  const isFirstLoad = useSharedValue(true);
  const isAction = useSharedValue(false);
  const { selectedDateString, currentTabIndex, showCalendar } =
    useResourceBookingProvider();

  /**
   * get query to set in redux
   */
  const _query = useMemo(
    () =>
      (query || "") + (query && resource_id ? "/" : "") + (resource_id || "") ||
      "all",
    [query, resource_id]
  );

  const getEvents: any = useCallback(
    (ids?: any) => {
      const _ids = ids || getBookingsByQuery("resource");
      return [...(_ids || [])]
        .map((id) => {
          const _booking = getBookings(id) as any;
          if (
            selectedDateString.value ===
            moment(_booking?.stime, "X").format("YYYY-MM-DD")
          ) {
            return {
              id,
              start: moment(_booking?.stime, "X").format("YYYY-MM-DD HH:mm:ss"),
              end: moment(_booking?.etime, "X").format("YYYY-MM-DD HH:mm:ss"),
              title: _booking?.name || "",
            };
          }
        })
        .filter(Boolean);
    },
    [_query, selectedDateString]
  );

  const _itemList = useBookingsByQuery(_query);
  useEffect(() => {
    if (!isAction.value && !isFirstLoad.value && !showCalendar.value) {
      setEvents(getEvents(_itemList));
    }
  }, [_itemList]);

  /**
   * request function
   */
  const [{ error: firstError }, onFirstLoad] = useAsyncFn(requestBookings, []);
  const [{ error: changeTabError }, onChangeTab] = useAsyncFn(
    requestBookings,
    []
  );
  const [{ loading: refreshing, error: refreshError }, onRefresh] = useAsyncFn(
    requestBookings,
    []
  );

  useAutoToastError(firstError);
  useAutoToastError(changeTabError);
  useAutoToastError(refreshError);

  /**
   * get params before submit
   */
  const submitParams = useMemo(() => {
    let result: any = {};

    if (params) {
      for (let key in params) {
        if (key !== mainKey && params[key]) result[key] = params[key];
      }
    }

    if (tab) result[mainKey] = tab;

    return result;
  }, [params, mainKey, tab]);

  /**
   * get prev params to determinded when params change
   */
  const prevParams = useSharedValue(submitParams);

  /**
   * onRefresh
   */
  const onLoadData = useCallback(() => {
    // isAction.value = true;
    onRefresh({
      // ...submitParams,
      start: getDSStartOfTFromDS("month", selectedDateString.value),
      end: getDSEndOfTFromDS("month", selectedDateString.value),
      resource_id: resource_id,
    }).then(() => {
      setEvents(getEvents() as any);
      // isAction.value = false;
    });
    return;
  }, [submitParams, onRefresh, _query, events.length, resource_id]);

  /**
   * First load
   */
  const _firstLoad = useCallback(() => {
    interactManager(() => {
      onFirstLoad({
        // ...submitParams,
        start: getDSStartOfTFromDS("month", selectedDateString.value),
        end: getDSEndOfTFromDS("month", selectedDateString.value),
        resource_id: resource_id,
      }).then(() => {
        setEvents(getEvents() as any);
        // onDataChange?.(_items);
        isFirstLoad.value = false;
        isAction.value = false;
        // setMounted(true);
      });
    }, 300);
  }, [_query, getEvents, resource_id]);

  useAnimatedReaction(
    () => currentTabIndex.value,
    (cur) => {
      if (index == cur && isFirstLoad.value) {
        // console.log("first Load");
        isAction.value = true;
        runOnJS(_firstLoad)();
      }
    },
    [index, _firstLoad]
  );

  /**
   * Change Tab Load
   */
  const _refresh = useCallback(
    (timeout: number, isClear?: boolean) => {
      // console.log("change Tab Load");
      isAction.value = true;
      // if (!isClear) setChangeParamsLoading(true);
      interactManager(
        () => {
          onChangeTab({
            // ...submitParams,
            start: getDSStartOfTFromDS("month", selectedDateString.value),
            end: getDSEndOfTFromDS("month", selectedDateString.value),
            resource_id: resource_id,
          }).then(() => {
            setEvents(getEvents());
            // onDataChange?.(_items);
            isAction.value = false;
            // setChangeParamsLoading(false);
          });
        },
        timeout,
        isClear
      );
    },
    [tab, submitParams, _query, selectedDateString, resource_id]
  );

  useAnimatedReaction(
    () => ({
      _index: currentTabIndex.value,
      _prevParams: prevParams.value,
      _selectedDateString: selectedDateString.value,
    }),
    ({ _index, _prevParams, _selectedDateString }, prev) => {
      // only fetch after first load
      if (!isFirstLoad.value) {
        // fetch when on current tab
        if (_index == index) {
          // fetch when change params
          if (
            JSON.stringify(submitParams) !== JSON.stringify(_prevParams) ||
            prev?._selectedDateString !== _selectedDateString
          ) {
            // console.log("change params");
            // page.value = 1;
            // isLoadMorable.value = true;
            prevParams.value = submitParams;
            runOnJS(_refresh)(1000);
          }
          // fetch when change tab
          else if (_index !== prev?._index) {
            // console.log("change tab");
            runOnJS(_refresh)(2000, true);
          }
        }
      }
    },
    [_refresh, submitParams, index]
  );

  // const onSetEvents = useCallback(() => {
  //   isAction.value = true;
  //   interactManager(() => {
  //     setEvents(getEvents());
  //     isAction.value = false;
  //   }, 1000);
  // }, []);
  //
  // useAnimatedReaction(
  //   () => ({
  //     _selectedDateString: selectedDateString.value,
  //     _index: currentTabIndex.value,
  //   }),
  //   (cur, prev) => {
  //     if (
  //       !isFirstLoad.value &&
  //       cur._index === index &&
  //       cur._selectedDateString !== prev?._selectedDateString
  //     ) {
  //       runOnJS(onSetEvents)();
  //     }
  //   },
  //   [onSetEvents, index]
  // );

  return useMemo(
    () => ({ events, isAction, onLoadData, refreshing }),
    [events, isAction, onLoadData, refreshing]
  );
};
