import { map, min, times } from "lodash";
import React, { memo, useCallback, useEffect, useMemo, useRef } from "react";
import { generateDay } from "react-native-calendars/src/dateutils";
import { calcTimeOffset } from "react-native-calendars/src/timeline/helpers/presenter";
import { TimelineHours } from "./TimeLineHours";
import { interactManager, memoForwardRef, styled } from "@true/core";
import {
  GestureHandlerRootView,
  ScrollView,
} from "react-native-gesture-handler";
import { UIText } from "@true/ui";
import { Text, View } from "react-native";
import Animated from "react-native-reanimated";
import { useResourceBookingProvider } from "@/screens/ResourceDetailScreen/provider";
import { ListEvents } from "@/screens/ResourceDetailScreen/components/calendar/MyTimeLine/ListEvents";
import { TimelineProps } from "@/screens/CalendarScreen/type";
import { constants, styleConstructor } from "@/screens/CalendarScreen/utils";
import {
  HOUR_BLOCK_HEIGHT,
  populateEvents,
} from "@/screens/CalendarScreen/packer";
import { NowIndicator } from "@/screens/CalendarScreen/components/calendar/MyTimeLine/NowIndicator";
import { useTimelineOffset } from "@/screens/CalendarScreen/hooks/useTimelineOffset";

export const Timeline = memoForwardRef(
  (props: TimelineProps, ref: React.Ref<any>) => {
    const {
      format24h = true,
      start = 0,
      end = 24,
      date = "",
      events,
      onEventPress,
      onBackgroundLongPress,
      onBackgroundLongPressOut,
      renderEvent,
      theme,
      scrollToFirst,
      scrollToNow,
      initialTime,
      showNowIndicator,
      scrollOffset,
      onChangeOffset,
      overlapEventsSpacing = 0,
      rightEdgeSpacing = 0,
      unavailableHours,
      unavailableHoursColor,
      eventTapped,
      numberOfDays = 1,
      timelineLeftInset = 0,
    } = props;

    const {
      panTopDotRef,
      panBottomDotRef,
      panBlockRef,

      scrollViewRef,
    } = useResourceBookingProvider();

    const _scrollViewRef = useRef<ScrollView>(null);

    const pageDates = useMemo(() => {
      return typeof date === "string" ? [date] : date;
    }, [date]);
    // const groupedEvents = useMemo(() => {
    //   return groupBy(events, (e) => getCalendarDateString(e.start));
    // }, [events]);
    // const pageEvents = useMemo(() => {
    //   return map(pageDates, (d) => groupedEvents[d] || []);
    // }, [pageDates, groupedEvents]);
    const calendarHeight = useRef((end - start) * HOUR_BLOCK_HEIGHT);
    const styles = useRef(
      styleConstructor(theme || props.styles, calendarHeight.current)
    );
    const { scrollEvents } = useTimelineOffset({
      onChangeOffset,
      scrollOffset,
      scrollViewRef: _scrollViewRef,
    });
    const width = useMemo(() => {
      return constants.screenWidth - timelineLeftInset;
    }, [timelineLeftInset]);
    const { calculatedEventGroup, calculatedEvents } = useMemo(() => {
      return populateEvents(events, {
        screenWidth: width / numberOfDays,
        dayStart: start,
        overlapEventsSpacing: overlapEventsSpacing / numberOfDays,
        rightEdgeSpacing: rightEdgeSpacing / numberOfDays,
      });
    }, [events, start, numberOfDays]);

    useEffect(() => {
      let initialPosition = 0;
      if (scrollToNow) {
        initialPosition = calcTimeOffset(HOUR_BLOCK_HEIGHT);
      } else if (scrollToFirst && calculatedEvents.length > 0) {
        initialPosition = min(map(calculatedEvents, "top")) ?? 0;
      } else if (initialTime) {
        initialPosition = calcTimeOffset(
          HOUR_BLOCK_HEIGHT,
          initialTime.hour,
          initialTime.minutes
        );
      }
      if (initialPosition) {
        setTimeout(() => {
          _scrollViewRef?.current?.scrollTo({
            y: Math.max(0, initialPosition - HOUR_BLOCK_HEIGHT),
            animated: true,
          });
        }, 0);
      }
    }, []);

    const _onEventPress = useCallback(
      (dateIndex: number, eventIndex: number) => {
        const event = calculatedEvents[eventIndex];
        if (eventTapped) {
          //TODO: remove after deprecation
          eventTapped(event);
        } else {
          onEventPress?.(event);
        }
      },
      [onEventPress, eventTapped, calculatedEvents]
    );

    const renderTimelineDay = useCallback(
      (dayIndex: number) => {
        const indexOfToday = pageDates.indexOf(
          generateDay(new Date().toString())
        );
        const left = timelineLeftInset + (indexOfToday * width) / numberOfDays;
        return (
          <React.Fragment key={dayIndex}>
            {calculatedEventGroup.length ? (
              <ListEvents
                dayIndex={dayIndex}
                calculatedEventGroup={calculatedEventGroup}
                onEventPress={_onEventPress}
                styles={styles.current}
                format24h={format24h}
                renderEvent={renderEvent}
              />
            ) : null}
            {indexOfToday !== -1 && showNowIndicator && (
              <NowIndicator
                width={width / numberOfDays - 16 * 2}
                left={left}
                styles={styles.current}
              />
            )}
          </React.Fragment>
        );
      },
      [pageDates, showNowIndicator, styles, calculatedEventGroup, _onEventPress]
    );

    // return null;

    return (
      <GestureHandlerRootView style={{ flex: 1 }}>
        <SScrollView
          ref={_scrollViewRef}
          waitFor={[panBottomDotRef, panTopDotRef, panBlockRef]}
          bounces={false}
          style={styles.current.container}
          contentContainerStyle={styles.current.contentStyle}
          showsVerticalScrollIndicator={false}
          scrollEventThrottle={40}
          {...scrollEvents}
        >
          <TimelineHours
            start={start}
            end={end}
            date={pageDates[0]}
            format24h={format24h}
            styles={styles.current}
            unavailableHours={unavailableHours}
            unavailableHoursColor={unavailableHoursColor}
            onBackgroundLongPress={onBackgroundLongPress}
            onBackgroundLongPressOut={onBackgroundLongPressOut}
            width={width}
            numberOfDays={numberOfDays}
            timelineLeftInset={timelineLeftInset}
            ref={ref}
          />
          {times(numberOfDays, renderTimelineDay)}
          <View style={styles.current.contentStyle} />
        </SScrollView>
        <SCaption
          style={{
            width: timelineLeftInset - 16,
          }}
        >
          00:00
        </SCaption>
      </GestureHandlerRootView>
    );
  }
);

const SScrollView = styled(ScrollView)`
  padding: 12px 16px;
  flex: 1;
`;

const SCaption = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
  position: absolute;
  text-align: center;
  top: 4px;
  left: 16px;
`;
