import React, {useCallback, useEffect, useMemo} from "react";
import {ScrollView} from "react-native";
import {goBack, memoForwardRef, styled, translate, useNavigationParams, useTheme,} from "@true/core";
import {AnimatedBottomSpace, LeftModalHeader, ScreenWrapper, UIButton, UIFlashList, UIText,} from "@true/ui";
import {Icons} from "@true/asset";
import moment from "moment/moment";
import {requestAvailableResoures} from "@/store/resources/function";
import {getResources, useResourcesByQuery} from "@/store/resources";
import {getResourceGroups, useResourceGroupsByQuery} from "@/store/groups";
import FastImage from "react-native-fast-image";
import {navigateToResourceDetailScreen} from "@/utils/navigation";
import {SvgIcon} from "@/assets/SvgIcons";
import {SvgXml} from "react-native-svg";

export interface SelectResourceScreenProps {
  sDate?: number;
  sTime?: number;
  eDate?: number;
  eTime?: number;
  onChangeValue: Function;
}

export const SelectResourceScreen = memoForwardRef(() => {
  const { sDate, sTime, eDate, eTime, onChangeValue } =
    useNavigationParams<SelectResourceScreenProps>();

  const startDate = moment.unix(sDate || 0).format("DD/MM/YYYY");
  const startTime = moment.unix(sTime || 0).format("HH:mm");
  const endDate = moment.unix(eDate || 0).format("DD/MM/YYYY");
  const endTime = moment.unix(eTime || 0).format("HH:mm");

  const startDateTime = moment(`${startDate} ${startTime}`, "DD/MM/YYYY HH:mm");
  const endDateTime = moment(`${endDate} ${endTime}`, "DD/MM/YYYY HH:mm");

  const startTimestamp = startDateTime.unix();
  const endTimestamp = endDateTime.unix();

  useEffect(() => {
    requestAvailableResoures({
      start: startTimestamp,
      end: endTimestamp,
    }).then();
  }, [startTimestamp, endTimestamp]);

  const availableResourceIds = useResourcesByQuery("available");

  const groupIds = useResourceGroupsByQuery("all");

  const availableResource = useMemo(() => {
    const groupsWithResources: any[] = [];

    groupIds.forEach((groupId) => {
      const group = getResourceGroups(groupId);
      const groupResource = availableResourceIds.map((resourceId) => {
        const resource = getResources(resourceId);
        if (resource?.group_id == group?.id) {
          return {
            id: resource?.id,
            name: resource?.name,
            content: resource?.content,
            thumbnail: resource?.thumbnail,
          };
        }
        return null;
      });

      if (groupResource.some((resource) => resource)) {
        // Nu c t nht mt ti nguyn thuc nhm, thm nhm vo mng kt qu
        groupsWithResources.push(group?.name);
        groupsWithResources.push(...groupResource.filter(Boolean));
      }
    });

    return groupsWithResources;
  }, [groupIds, availableResourceIds]);

  const renderSectionHeader = useCallback(
    (item: string) => {
      const theme = useTheme();

      const icon = useMemo(() => {
        let _icon: string = "";
        groupIds.map((groupId) => {
          if (getResourceGroups(groupId)?.name === item) {
            const groupIcon = (getResourceGroups(groupId)?.icon || "").replace(
              "-",
              "_"
            );
            _icon = groupIcon;
          }
        });
        return _icon;
      }, [groupIds, item]);

      return (
        <SRow>
          <SViewIcon>
            {SvgIcon.hasOwnProperty(icon) ? (
              <SvgXml
                // @ts-ignore
                xml={SvgIcon[icon]}
                width={16}
                height={16}
                fill={theme.neutral1}
              />
            ) : null}
            {/*<SIcon source={Icons.Outlined.COMPUTER} />*/}
          </SViewIcon>
          <STextGroup>{item}</STextGroup>
        </SRow>
      );
    },
    [groupIds]
  );

  const renderItem = useCallback((item: any) => {
    return (
      <SViewItem
        onPress={() => navigateToResourceDetailScreen({ id: item?.id })}
      >
        <SView>
          <SImage source={{ uri: item.thumbnail }} />
          <SViewInfo>
            <STextName numberOfLines={2}>{item.name}</STextName>
            <STextDesc>
              {item.content
                ? item.content
                : translate("booking.no_description")}
            </STextDesc>
          </SViewInfo>
        </SView>

        <SButton onPress={() => onChangeValue(item.id)} />
      </SViewItem>
    );
  }, []);

  return (
    <SScreenWrapper>
      <LeftModalHeader
        title={translate("booking.select_a_resource")}
        rightComponent={
          <SCloseButton source={Icons.Filled.X_MARK} onPress={goBack} />
        }
      />
      <ScrollView>
        <SViewTime>
          <STextInfo>
            {translate("booking.display_available_resources")}
          </STextInfo>
          <STextTime>
            {translate("booking.from")}{" "}
            {moment.unix(startTimestamp).format("DD/MM - HH:mm")}{" "}
            {translate("booking.to")}{" "}
            {moment.unix(endTimestamp).format("DD/MM - HH:mm")}
          </STextTime>
        </SViewTime>
        <UIFlashList
          data={availableResource}
          renderItem={({ item }) => {
            if (typeof item === "string") {
              // sectionHeader(item);
              // Rendering header
              return renderSectionHeader(item);
            } else {
              // Render item
              return renderItem(item);
            }
          }}
          getItemType={(item) => {
            // To achieve better performance, specify the type based on the item
            return typeof item === "string" ? "sectionHeader" : "row";
          }}
          estimatedItemSize={100}
        />
        <AnimatedBottomSpace />
      </ScrollView>
    </SScreenWrapper>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.white};
`;

const SCloseButton = styled(UIButton)`
  width: 24px;
  height: 24px;
  margin-right: 16px;
`;

const SViewTime = styled.View`
  background-color: ${(p) => p.theme.neutral8};
  padding: 8px 16px;
  margin-bottom: 4px;
`;

const STextInfo = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
  margin-bottom: 4px;
`;

const STextTime = styled(UIText.BodyMedium500)`
  color: ${(p) => p.theme.neutral1};
`;

const SRow = styled.View`
  flex-direction: row;
  margin: 20px 16px 8px 16px;
`;

const SViewItem = styled.TouchableOpacity`
  flex-direction: row;
  margin: 0 16px;
  padding: 8px 0;
  align-items: center;
  justify-content: space-between;
  border-bottom-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

const SView = styled.View`
  flex-direction: row;
  flex: 1;
`;

const SViewIcon = styled.View`
  padding: 4px;
  background-color: ${(p) => p.theme.neutral4};
  border-radius: 4px;
  margin-right: 8px;
`;

const SIcon = styled.Image`
  height: 16px;
  width: 16px;
`;

const STextGroup = styled(UIText.HeaderMedium)`
  color: ${(p) => p.theme.neutral1};
`;

const SImage = styled(FastImage)`
  width: 80px;
  height: 56px;
  border-radius: 8px;
  margin-right: 12px;
`;

const SViewInfo = styled.View`
  justify-content: center;
  flex: 1;
`;

const STextName = styled(UIText.BodyMedium500)`
  color: ${(p) => p.theme.neutral1};
`;

const STextDesc = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
`;

const SButton = styled(UIButton)`
  height: 20px;
  width: 20px;
  border-radius: 24px;
  border-width: 1.5px;
  border-color: ${(p) => p.theme.neutral3};
`;
