import RNFetchBlob from "rn-fetch-blob";
import FileViewer from "react-native-file-viewer";
import { createNavigate } from "@true/core";
import { OfficeFileScreenProps } from "@/screens/OfficeFileScreen";

// const android = RNFetchBlob.android;

const OfficeExts = ["docx", "doc", "xls", "xlt", "xlsx", "ppt", "pptx"];

export async function showFile(fileUrl: string) {
  const ext = fileUrl.split(/[#?]/)[0].split(".").pop()?.trim() || "";

  const filename = fileUrl.split("/").pop() || "";

  if (OfficeExts.indexOf(ext) !== -1) {
    return createNavigate<OfficeFileScreenProps>("OfficeFileScreen")({
      title: filename,
      url: fileUrl,
    });
  }

  const res = await RNFetchBlob.config({
    fileCache: true,
    appendExt: ext,
  }).fetch("GET", fileUrl);

  const downloadFile = res.path();
  // Platform.OS === 'android' ? 'file://' + res.path() : '' + res.path();
  //   await android.actionViewIntent(downloadFile, 'application/*')

  await FileViewer.open(downloadFile, {
    showOpenWithDialog: true,
    onDismiss: () => RNFetchBlob.fs.unlink(res.path()),
  });
}
