import React, { memo, useCallback, useState } from "react";
import {
  runOnJS,
  runOnUI,
  SharedValue,
  useAnimatedReaction,
} from "react-native-reanimated";
import { goBack, styled, translate } from "@true/core";
import { UIText } from "@true/ui";
import { openSelectResourceScreen } from "@/utils/navigation";
import { ResourceCard } from "@/screens/BookingDetailScreen/components/ResourceCard";
import { Icons } from "@true/asset";

interface Props {
  params: SharedValue<{ [p: string]: any }>;
}

export const BookingResource = memo(({ params }: Props) => {
  const [resourceId, setResourceId] = useState(params.value["resource_id"]);

  useAnimatedReaction(
    () => params.value["resource_id"],
    (cur) => runOnJS(setResourceId)(cur)
  );

  const onChangeValue = useCallback(
    (id: any) => {
      runOnUI((id: any) => {
        "worklet";

        params.value = {
          ...params.value,
          resource_id: id,
        };
      })(id);

      goBack();
    },
    [params]
  );

  return (
    <>
      {resourceId ? (
        <SViewResource
          onPress={() =>
            openSelectResourceScreen({
              sDate: params.value["start-date"],
              sTime: params.value["start-time"],
              eDate: params.value["end-date"],
              eTime: params.value["end-time"],
              onChangeValue: onChangeValue,
            })
          }
        >
          <ResourceCard resourceId={resourceId} />
        </SViewResource>
      ) : (
        <SWrapper
          onPress={() =>
            openSelectResourceScreen({
              sDate: params.value["start-date"],
              sTime: params.value["start-time"],
              eDate: params.value["end-date"],
              eTime: params.value["end-time"],
              onChangeValue: onChangeValue,
            })
          }
        >
          <SIcon source={Icons.Outlined.COMPUTER} />
          <SView>
            <SText>{translate("booking.select_resource")}</SText>
          </SView>
        </SWrapper>
      )}
    </>
  );
});

const SWrapper = styled.TouchableOpacity`
  flex-direction: row;
  background-color: ${(p) => p.theme.white};
  width: 100%;
  padding: 26px 0;
  border-radius: 8px;
  margin-bottom: 16px;
  justify-content: center;
  align-content: center;
  border-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

const SIcon = styled.Image`
  height: 40px;
  width: 40px;
  tint-color: ${(p) => p.theme.neutral2};
  margin-right: 16px;
`;

const SViewResource = styled.TouchableOpacity`
  margin-bottom: 12px;
`;

const SView = styled.View`
  background-color: #eeeef2;
  justify-content: center;
  border-radius: 8px;
`;

const SText = styled(UIText.ButtonMedium)`
  color: ${(p) => p.theme.neutral2};
  padding: 0 16px;
`;
