import React, { useCallback, useRef } from "react";
import { memoForwardRef, styled, useTheme } from "@true/core";
import { ScrollableTabView } from "@true/ui";
import { LocaleConfig } from "react-native-calendars/src";
import { Timeline } from "./calendar/MyTimeLine";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";
import { useSelectedItems } from "@/screens/CalendarScreen/hooks/useSelectedItems";
import { ListBookingsCard } from "@/screens/CalendarScreen/components/calendar/ListBookingsCard";
import {
  LayoutChangeEvent,
  NativeScrollEvent,
  RefreshControl,
} from "react-native";
import ProgressBar from "@/components/ProgressBar";
import Animated, {
  useAnimatedStyle,
  useWorkletCallback,
  withTiming,
} from "react-native-reanimated";
import { useRequestBookings } from "../hooks/useRequestBookings";
import _ from "lodash";

interface BookingCalendarProps {
  index: number;
  tab?: string;
}

LocaleConfig.locales["en"] = {
  monthNames: [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ],
  dayNames: [
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
    "Sunday",
  ],
  dayNamesShort: ["S", "M", "T", "W", "T", "F", "S"],
};

LocaleConfig.defaultLocale = "en";

const timelineProps = {
  format24h: true,
  start: 0,
  end: 24,
  // unavailableHours: [
  //   { start: 0, end: 6 },
  //   { start: 22, end: 24 },
  // ],
  // unavailableHoursColor: "white",
  overlapEventsSpacing: 8,
  rightEdgeSpacing: 16 * 3 + 34,
};

const INITIAL_TIME = { hour: 9, minutes: 0 };

export const ListBookings = memoForwardRef(
  ({ index, tab }: BookingCalendarProps) => {
    const theme = useTheme();
    const {
      selectedDateString,
      selectedType,
      scrollViewSize,
      containerSize,
      scrollOffsetY,
      aViewRef,
      scrollInit,
      touchTranslate,
    } = useBookingProvider();
    const { selectedDate, type } = useSelectedItems(
      selectedDateString,
      selectedType
    );
    const scrollViewRef = useRef<Animated.ScrollView>(null);

    const { events, isAction, onLoadData, refreshing } = useRequestBookings({
      index,
      tab,
    });

    const animatedLoadingStyle = useAnimatedStyle(() => ({
      opacity: withTiming(isAction.value ? 1 : 0),
    }));

    const onContainerLayoutList = useCallback((e: LayoutChangeEvent) => {
      containerSize.value = e.nativeEvent.layout.height;
    }, []);

    const onContentSizeChange = useCallback((w: number, h: number) => {
      scrollViewSize.value = h;
    }, []);

    const onScroll = useWorkletCallback((e: NativeScrollEvent) => {
      scrollOffsetY.value = e.contentOffset.y;
    }, []);

    const onMomentumScrollEnd = useCallback(
      _.debounce(() => {
        // console.log("ending", touchTranslate.value);
        // touchTranslate.value += scrollInit.value - scrollOffsetY.value;
        // scrollInit.value = scrollOffsetY.value;
      }, 100),
      []
    );

    return (
      <>
        <ScrollableTabView.ScrollView
          index={index}
          ref={scrollViewRef}
          refreshControl={() => (
            <RefreshControl refreshing={refreshing} onRefresh={onLoadData} />
          )}
          // onContentSizeChange={onContentSizeChange}
          // onContainerLayoutList={onContainerLayoutList}
          onScroll={onScroll}
          // onMomentumScrollEnd={onMomentumScrollEnd}
        >
          {type === "list" ? (
            <ListBookingsCard events={events} />
          ) : (
            <Timeline
              events={events}
              // timelineProps={timelineProps}
              showNowIndicator
              scrollToFirst
              initialTime={INITIAL_TIME}
              date={selectedDate}
              ref={scrollViewRef}
              theme={{}}
              {...timelineProps}
              // renderItem={renderItem}
            />
          )}
        </ScrollableTabView.ScrollView>
        <ViewLoading style={animatedLoadingStyle}>
          <ProgressBar
            indeterminate
            backgroundColor={theme.primaryColor}
            trackColor={"transparent"}
          />
        </ViewLoading>
      </>
    );
  }
);

const ViewLoading = styled(Animated.View)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999;
`;
