import React, { memo, useCallback, useState } from "react";
import { memoForwardRef, styled } from "@true/core";
import Animated, {
  runOnJS,
  useAnimatedGestureHandler,
  useAnimatedReaction,
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";
import { PanGestureHandler } from "react-native-gesture-handler";
import { UITextStyle } from "@true/ui";
import { useResourceBookingProvider } from "@/screens/ResourceDetailScreen/provider";
import {HOURS_TEXT_WIDTH} from "@/screens/ResourceDetailScreen/components/calendar/MyTimeLine/TimeLineHours";
import {buildHourAndMinuteString, calcTimeByPosition, clamp} from "@/screens/CalendarScreen/utils";
import {HOUR_BLOCK_HEIGHT} from "@/screens/CalendarScreen/packer";

const AnimatedDateComp = memo(({ numberHours }: { numberHours: number }) => {
  const { posSelectTime } = useResourceBookingProvider();
  const [date, setDate] = useState(posSelectTime.date.value);

  useAnimatedReaction(
    () => posSelectTime.date.value,
    (cur) => {
      runOnJS(setDate)(cur);
    },
    [setDate]
  );

  const animatedTextStyle = useAnimatedStyle(
    () => ({
      opacity:
        posSelectTime.showDateTop.value || posSelectTime.showDateBottom.value
          ? 1
          : 0,
      top: !posSelectTime.showDateBottom.value
        ? posSelectTime.top.value - 8
        : posSelectTime.top.value + posSelectTime.height.value - 8,
    }),
    [numberHours]
  );

  return (
    <SCaption style={[UITextStyle.caption_medium_400, animatedTextStyle]}>
      {date}
    </SCaption>
  );
});

interface Props {
  numberHours: number;
  timelineLeftInset: number;
}

export const PanGestureSelectTime = memoForwardRef(
  ({ numberHours, timelineLeftInset }: Props, ref: React.Ref<any>) => {
    const {
      posSelectTime,
      panTopDotRef,
      panBottomDotRef,
      panBlockRef,
      isTouchActiveNative,
      touchTranslate,
      aViewRef,
      scrollInit,
      scrollOffsetY,
    } = useResourceBookingProvider();
    const translationY = useSharedValue(0);
    const aOffsetY = useSharedValue(0);
    const touchRelativeY = useSharedValue(0);
    const _touchTranslate = useSharedValue(0);

    const isScrollUp = useSharedValue(false);

    const animatedStyle = useAnimatedStyle(() => {
      return {
        top: posSelectTime.top.value,
        height: posSelectTime.height.value,
        left: timelineLeftInset ? timelineLeftInset - 16 : HOURS_TEXT_WIDTH + 8,
        opacity: posSelectTime.show.value ? 1 : 0,
        zIndex: posSelectTime.show.value ? 10000 : -1,
      };
    }, [timelineLeftInset]);

    const onCalcByPanGesture = useCallback((top: number) => {
      const { hour, minutes } = calcTimeByPosition(
        top + touchTranslate.value,
        HOUR_BLOCK_HEIGHT
      );
      posSelectTime.date.value = buildHourAndMinuteString(hour, minutes);
    }, []);

    const onActivePanHandler = useCallback(
      (ev: any, ctx: any) => {
        if (ev.velocityY >= 0) {
          touchTranslate.value +=
            ev.translationY > touchTranslate.value + 10
              ? HOUR_BLOCK_HEIGHT * 0.25
              : 0;
        } else {
          touchTranslate.value -=
            ev.translationY < touchTranslate.value - 10
              ? HOUR_BLOCK_HEIGHT * 0.25
              : 0;
        }

        onCalcByPanGesture(ctx.top);
      },
      [onCalcByPanGesture]
    );

    const gestureHandler = useAnimatedGestureHandler({
      onStart: (_ev, ctx) => {
        const _top = posSelectTime.top.value;
        ctx.top = _top;
        posSelectTime.showDateTop.value = true;
        isTouchActiveNative.value = true;
        runOnJS(onCalcByPanGesture)(_top);
        // const a = measure(aViewRef);
        // console.log(a);
      },
      onActive: (ev, ctx: any) => {
        // touchRelativeY.value = ev.y;
        // if (
        //   (ev.velocityY < 0 && ev.absoluteY - ev.y < 185) ||
        //   ev.absoluteY < 185
        // ) {
        //   console.log(touchTranslate.value);
        //   if (!isScrollUp.value) {
        //     scrollInit.value = scrollOffsetY.value;
        //   }
        //   isScrollUp.value = true;
        //   return;
        // } else {
        //   if (isScrollUp.value) {
        //     console.log(
        //       scrollInit.value - scrollOffsetY.value,
        //       touchTranslate.value,
        //       ev.translationY
        //     );
        //     _touchTranslate.value += scrollInit.value - scrollOffsetY.value;
        //   }
        //
        //   scrollInit.value = scrollOffsetY.value;
        //   isScrollUp.value = false;
        // }

        runOnJS(onActivePanHandler)(ev, ctx);

        posSelectTime.top.value = clamp(
          ctx.top + touchTranslate.value,
          0,
          numberHours * HOUR_BLOCK_HEIGHT - posSelectTime.height.value
        );
      },
      onFinish: (_ev, ctx) => {
        touchTranslate.value = 0;
        _touchTranslate.value = 0;
        posSelectTime.showDateTop.value = false;
        isTouchActiveNative.value = false;
        // isScrollUp.value = false;
        // posSelectTime.top.value =
        //   posSelectTime.top.value - (scrollInit.value - scrollOffsetY.value);
      },
    });

    const onTopDotGestureHandler = useAnimatedGestureHandler({
      onStart: (_ev, ctx) => {
        const _top = posSelectTime.top.value;
        ctx.top = _top;
        ctx.height = posSelectTime.height.value;
        posSelectTime.showDateTop.value = true;
        runOnJS(onCalcByPanGesture)(_top);
      },
      onActive: (ev, ctx: any) => {
        runOnJS(onActivePanHandler)(ev, ctx);

        posSelectTime.top.value = clamp(
          ctx.top + touchTranslate.value,
          0,
          ctx.top + ctx.height - HOUR_BLOCK_HEIGHT * 0.25
        );

        posSelectTime.height.value = clamp(
          ctx.height - touchTranslate.value,
          HOUR_BLOCK_HEIGHT * 0.25,
          ctx.top + ctx.height
        );
      },
      onFinish: (_ev, ctx) => {
        touchTranslate.value = 0;
        posSelectTime.showDateTop.value = false;
      },
    });

    const onBottomDotGestureHandler = useAnimatedGestureHandler({
      onStart: (_ev, ctx) => {
        const _top = posSelectTime.height.value + posSelectTime.top.value;
        ctx.height = posSelectTime.height.value;
        ctx.top = _top;
        posSelectTime.showDateBottom.value = true;
        posSelectTime.showDateTop.value = false;
        runOnJS(onCalcByPanGesture)(_top);
      },
      onActive: (ev, ctx: any) => {
        runOnJS(onActivePanHandler)(ev, ctx);

        posSelectTime.height.value = clamp(
          ctx.height + touchTranslate.value,
          HOUR_BLOCK_HEIGHT * 0.25,
          numberHours * HOUR_BLOCK_HEIGHT - posSelectTime.top.value
        );
      },
      onFinish: (_ev, ctx) => {
        touchTranslate.value = 0;
        posSelectTime.showDateBottom.value = false;
      },
    });

    // useAutoScroll(isScrollUp, ref);

    return (
      <>
        <PanGestureHandler
          waitFor={[panBottomDotRef, panTopDotRef]}
          shouldCancelWhenOutside={false}
          ref={panBlockRef}
          onGestureEvent={gestureHandler}
        >
          <SWrapper style={animatedStyle}>
            <PanGestureHandler
              ref={panTopDotRef}
              onGestureEvent={onTopDotGestureHandler}
            >
              <STopDotWrapper>
                <SDot />
              </STopDotWrapper>
            </PanGestureHandler>
            <SOverlayBg />
            <PanGestureHandler
              ref={panBottomDotRef}
              onGestureEvent={onBottomDotGestureHandler}
            >
              <SBottomDotWrapper>
                <SDot />
              </SBottomDotWrapper>
            </PanGestureHandler>
          </SWrapper>
        </PanGestureHandler>
        <AnimatedDateComp numberHours={numberHours} />
      </>
    );
  }
);

const SWrapper = styled(Animated.View)`
  position: absolute;
  right: 0;
  border-radius: 8px;
  border: 2px solid ${(p) => p.theme.blue1};
`;

const SCaption = styled(Animated.Text)`
  color: ${(p) => p.theme.neutral2};
  position: absolute;
  text-align: center;
`;

const SOverlayBg = styled.View`
  flex: 1;
  background-color: ${(p) => p.theme.blue1};
  opacity: 0.2;
`;

const STopDotWrapper = styled(Animated.View)`
  left: 8px;
  top: -8px;
  position: absolute;
  height: 16px;
  width: 16px;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const SBottomDotWrapper = styled(Animated.View)`
  right: 8px;
  bottom: -8px;
  position: absolute;
  height: 16px;
  width: 16px;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const SDot = styled.View`
  background-color: ${(p) => p.theme.blue1}
  width: 8px;
  height: 8px;
  border-radius: 100px;
`;
