import React, { memo, PropsWithChildren, useEffect } from "react";
import Modal from "react-native-modal";
import FastImage from "react-native-fast-image";
import { Dimensions, StyleSheet } from "react-native";
import {
  Fonts,
  interactManager,
  styled,
  translate,
  useTheme,
} from "@true/core";
import { UIBottomButton, UIText } from "@true/ui";

interface Props {
  isVisible: boolean;
  onCloseRequest: () => any;
  disableSwipe?: boolean;
  title: string;
  duration?: number;
  onPress: () => any;
}

export const ConfirmDialog = memo(
  ({
    children,
    isVisible,
    onCloseRequest,
    disableSwipe,
    duration,
    onPress,
    title,
  }: PropsWithChildren<Props>) => {
    useEffect(() => {
      interactManager(() => {
        onCloseRequest();
      }, duration || 1000);
    }, [duration]);

    const theme = useTheme();

    return (
      <Modal
        isVisible={isVisible}
        avoidKeyboard={true}
        // presentationStyle={'pageSheet'}
        swipeDirection={disableSwipe ? undefined : "down"}
        propagateSwipe={!disableSwipe}
        backdropTransitionOutTiming={0}
        // backdropOpacity={0.9}
        hideModalContentWhileAnimating={true}
        useNativeDriver={true}
        animationIn={"zoomIn"}
        style={{ alignItems: "center" }}
        animationOut={"zoomOut"}
      >
        <SWrapper>
          <STitle>{title}</STitle>
          <SView>{children}</SView>

          <SBottomButton
            buttons={[
              {
                title: translate("booking.cancel"),
                onPress: onCloseRequest,
                textColor: theme.primaryColor,
                backgroundColor: theme.white,
                style: {
                  borderWidth: 1,
                  borderColor: theme.primaryColor,
                },
                textStyle: styles.textStyle,
                heightButton: 40,
              },
              {
                title: translate("booking.confirm"),
                onPress: onPress,
                textColor: theme.white,
                backgroundColor: theme.primaryColor,
                textStyle: { ...styles.textStyle, marginRight: 0 },
                heightButton: 40,
              },
            ]}
          />
        </SWrapper>
      </Modal>
    );
  }
);

const styles = StyleSheet.create({
  textStyle: {
    fontFamily: Fonts.Medium,
    fontSize: 16,
    lineHeight: 24,
  },
});

const SWrapper = styled.View`
  border-radius: 16px;
  background-color: ${(props) => props.theme.backgroundColor};
  overflow: hidden;
  align-items: center;
  padding-top: 16px;
  width: ${(Dimensions.get("window").width * 2) / 2.5}px;
`;

const SView = styled.View`
  width: 90%;
  margin-top: 16px;
`;

const SBottomButton = styled(UIBottomButton)`
  padding: 8px 16px 0 16px;
  background-color: ${(p) => p.theme.white};
  width: 100%;
`;

const STitle = styled(UIText.HeaderLarge)`
  color: ${(p) => p.theme.neutral1};
`;
