import { batch } from "react-redux";
import { Core, Fetch } from "@true/core";
import {
  EStatusBooking,
  IBooking,
  IRepeatedBooking,
} from "@/store/booking/type";
import { getBookings, setBookingsQuery, syncBookings } from "@/store/booking";
import { getDSEndOfTFromDS, getDSStartOfTFromDS } from "@/utils/calendar";
import { getAllBookingInThisMonth } from "@/services/BookingService";
import { ICreateBookingmParams } from "@/screens/CreateBookingScreen";

export const requestBookings = async ({
  start,
  end,
  filter,
  status,
  resource_id,
  q,
}: {
  start?: string;
  end?: string;
  filter?: string;
  status?: string;
  resource_id?: string;
  q?: string;
}) => {
  const _start = getDSStartOfTFromDS("month", start);
  const _end = getDSEndOfTFromDS("month", end);
  let query = `start=${_start}&end=${_end}`;

  if (resource_id) {
    query += `&filter=all&resource_id=${resource_id}`;
  }

  if (filter) {
    query += `&filter=${filter}`;
  }

  if (status) {
    query += `&status=${status}`;
  }

  if (q && q !== "") {
    query += `&filter=all&q=${q}`;
  }

  const { data } = await Fetch.postWithToken<{
    bookings: IBooking[];
    repeated_bookings: IRepeatedBooking[];
  }>(`@true/booking/ajax/api/mobile/bookings?${query}`);

  const _data = getAllBookingInThisMonth(
    _start,
    _end,
    data?.bookings,
    data?.repeated_bookings
  );

  batch(() => {
    if (_data && _data.length) {
      syncBookings(_data);
      setBookingsQuery((prev) => ({
        ...prev,
        [filter || "all"]: _data.map((item) => item.id.toString()),
      }));
    }
    if (resource_id) {
      syncBookings(_data);
      setBookingsQuery((prev) => ({
        ...prev,
        ["resource"]: _data.map((item) => item.id.toString()),
      }));
    }
    if (q) {
      syncBookings(_data);
      setBookingsQuery((prev) => ({
        ...prev,
        ["search"]: _data.map((item) => item.id.toString()),
      }));
    }
  });
};

export const requestCreateBooking = async (params: ICreateBookingmParams) => {
  console.log("params", params);
  const { data } = await Fetch.postWithToken<{
    booking: IBooking;
  }>(`@${Core.systemPath}/booking/ajax/api/mobile/booking/create`, params);

  batch(() => {
    if (data.booking) {
      syncBookings([data.booking]);
      setBookingsQuery((prev: any) => ({
        ...prev,
        ["all"]: [data.booking.id, ...(prev["all"] || [])],
      }));
    }
  });

  return {
    booking: {
      ...data.booking,
    },
  };
};

