export const requestUnfollow = async (params: {
  obj_id: string | number;
  obj_type?: "bookings";
  flag: number;
  user_id: string;
}) => {
  const { data } = await Fetch.postWithToken<{
    booking: IBooking;
  }>("@true/booking/ajax/api/mobile/booking/follow", params);

  batch(() => {
    if (data.booking) {
      syncBookings([data.booking]);
    }
  });
};

export const requestCheckOverlap = async (params: {
  id: string | number | undefined;
  stime?: number;
  etime?: number;
}) => {
  const { data } = await Fetch.postWithToken<{
    coincident: true | null;
  }>("@true/booking/ajax/api/mobile/booking/repeated/check.overlap", params);

  return data.coincident;
};

export const requestApprove = async (params: {
  id: string | number;
  resource_id?: number;
  note: string;
}) => {
  const { data } = await Fetch.postWithToken(
    "@true/booking/ajax/api/mobile/booking/approve",
    params
  );

  const _booking = getBookings(params.id);

  batch(() => {
    if (_booking) {
      syncBookings([
        {
          ..._booking,
          status: EStatusBooking.APPROVED,
        },
      ]);
    }
  });
};

