import {AnimatedBottomSpace, DynamicHeader, ScreenWrapper} from '@true/ui';
import React, {memo} from 'react';
import {useNavigationParams} from '@true/core';
import {FileWebView} from "@/screens/OfficeFileScreen/FileWebView";

export interface OfficeFileScreenProps {
  title: string;
  url: string;
}

export const OfficeFileScreen = memo(() => {
  const {title, url} = useNavigationParams<OfficeFileScreenProps>();

  return (
    <ScreenWrapper>
      <DynamicHeader title={title} />
      <FileWebView url={url} />
      <AnimatedBottomSpace />
    </ScreenWrapper>
  );
});
