import {
  runOnJS,
  SharedValue,
  useAnimatedReaction,
} from "react-native-reanimated";
import { useCallback, useState } from "react";
import { interactManager } from "@true/core";

export const useSelectedItems = (
  dateString?: SharedValue<string>,
  selectedType?: SharedValue<string>
) => {
  const [selectedDate, setDate] = useState(dateString ? dateString.value : "");
  const [type, setType] = useState(selectedType ? selectedType.value : "");

  useAnimatedReaction(
    () => dateString?.value,
    (cur) => {
      if (cur && cur !== "") {
        runOnJS(setDate)(cur);
      }
    },
    [setDate]
  );

  const onSetType = useCallback(
    (_type: string) => {
      interactManager(() => setType(_type), 300);
    },
    [setType]
  );

  useAnimatedReaction(
    () => selectedType?.value,
    (cur) => {
      if (cur && cur !== "") {
        runOnJS(onSetType)(cur);
      }
    },
    [onSetType]
  );

  return {
    selectedDate,
    type,
  };
};
