import React, { useCallback, useState } from "react";
import { ScrollView } from "react-native";
import { Fonts, memoForwardRef, styled, translate } from "@true/core";
import {
  AnimatedBottomSpace,
  LeftModalHeader,
  ScreenWrapper,
  UIButton,
  UIFlashList,
  UIText,
} from "@true/ui";
import { useResourceGroupsByQuery } from "@/store/groups";
import { ResourceGroupItem } from "@/screens/ResoucesScreen/components/ResourceGroupItem";
import { Icons, IMAGE_EMPTY_RESOURCE_GROUP } from "@true/asset";
import { openSelectTimeScreen } from "@/utils/navigation";
import moment from "moment";
import { EmptyViewCenter } from "@/components/EmptyView/EmptyViewCenter";

export const ResourcesScreen = memoForwardRef(() => {
  const groupIds = useResourceGroupsByQuery("all");
  const [start, setStart] = useState(0);
  const [end, setEnd] = useState(0);

  const renderEmptyView = useCallback(() => {
    return (
      <EmptyViewCenter
        source={IMAGE_EMPTY_RESOURCE_GROUP}
        title={translate("booking.empty_group")}
        subTitle={translate("booking.no_resource_group")}
      />
    );
  }, []);

  return (
    <SScreenWrapper>
      <LeftModalHeader
        title={translate("booking.resource_groups")}
        hideCloseIcon={true}
      />
      <SView>
        <STextTitle>
          {translate("booking.display_available_resources")}:
        </STextTitle>
        <SButton
          title={
            start === 0 && end === 0
              ? translate("booking.all_time")
              : `${translate("booking.from")} ${moment
                  .unix(start)
                  .format("DD/MM - HH:mm")} ${translate("booking.to")} ${moment
                  .unix(end)
                  .format("DD/MM - HH:mm")}`
          }
          onPress={() => openSelectTimeScreen({ start, end, setStart, setEnd })}
        />
      </SView>
      <SScrollView>
        <UIFlashList
          data={groupIds}
          numColumns={2}
          renderItem={({ item, index }) => (
            <ResourceGroupItem id={item} start={start} />
          )}
          extraData={start}
          ListEmptyComponent={renderEmptyView}
        />
        <AnimatedBottomSpace />
      </SScrollView>
    </SScreenWrapper>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.white};
`;

const SScrollView = styled(ScrollView)`
  padding-left: 16px;
`;

const SView = styled.View`
  background-color: ${(p) => p.theme.neutral8};
  padding: 8px 16px;
`;

const STextTitle = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
`;

const SButton = styled(UIButton).attrs((p) => ({
  rightSource: Icons.Filled.CHEVRON_DOWN,
  rightImageSize: 14,
  rightImageStyle: { tintColor: p.theme.neutral2, marginLeft: 8 },
  textStyle: {
    fontFamily: Fonts.Medium,
    fontSize: 14,
    lineHeight: 20,
    color: p.theme.neutral1,
  },
}))`
  margin-top: 4px;
`;
