import { batch } from "react-redux";
import { Core, Fetch } from "@true/core";
import { IResource } from "@/store/resources/type";
import {
  getResourcesByQuery,
  setResourcesQuery,
  syncResources,
} from "@/store/resources/index";

export const requestResoures = async (params: {}) => {
  const { data } = await Fetch.postWithToken<{
    resources: IResource[];
  }>(`@${Core.systemPath}/booking/ajax/api/mobile/resources`, params);

  batch(() => {
    if (data.resources) {
      syncResources(data.resources);
      setResourcesQuery((prev) => ({
        ...prev,
        ["all"]: data.resources.map((item) => item.id.toString()),
      }));
    }
  });
};

export const requestGetFavResoures = async () => {
  const { data } = await Fetch.postWithToken<{
    resources: number[];
  }>(`@${Core.systemPath}/booking/ajax/api/mobile/favorite.resources`);

  batch(() => {
    if (data.resources) {
      setResourcesQuery((prev) => ({
        ...prev,
        ["favourite"]: data.resources.map((item) => item.toString()),
      }));
    }
  });
};

export const requestFavResoures = async (params: { id: string }) => {
  const { data } = await Fetch.postWithToken<{
    resources: number[];
  }>(
    `@${Core.systemPath}/booking/ajax/api/mobile/resource/set.favorite`,
    params
  );

  const resourceIds = getResourcesByQuery("favourite");
  if (resourceIds.includes(params.id)) {
    batch(() => {
      setResourcesQuery((prev) => ({
        ...prev,
        ["favourite"]: [...(prev["favourite"] || [])].filter(
          (id) => id !== params.id
        ),
      }));
    });
  } else {
    batch(() => {
      setResourcesQuery((prev) => ({
        ...prev,
        ["favourite"]: [params.id, ...(prev["favourite"] || [])],
      }));
    });
  }
};

export const requestAvailableResoures = async (params: {
  start?: string | number;
  end?: string | number;
}) => {
  const { data } = await Fetch.postWithToken<{
    resources: IResource[];
  }>(
    `@${Core.systemPath}/booking/ajax/api/mobile/available.resources?start=${params.start}&end=${params.end}`
  );

  batch(() => {
    if (data.resources) {
      syncResources(data.resources);
      setResourcesQuery((prev) => ({
        ...prev,
        ["available"]: data.resources.map((item) => item.id.toString()),
      }));
    }
  });
};
